.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  margin: 0 !important;
  padding: 0 !important;
}

:root {
  --primary-font-color: white;
  --primary-button-color: grey;
}

.img {
  height: 20vh;
  width: 10rem;
  padding-top: 2rem;
}

.Navigation-container {
  background-color: var(--primary-button-color);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 5px 10px;
  max-width: 100%;
  left: 0 !important;
  right: 0 !important;
}

.Navigation-header {
  color: var(--primary-font-color);
  font-size: 40px;
  font-style: bold;
}
.nav-links {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  list-style: none;
}

.text-decoration-none {
  text-decoration: none;
}

.link {
  border-radius: 0.2rem;
  margin: 1rem;
  color: black;
}

.icon-badge {
  border-radius: 50%;
  border: none;
  position: absolute; /*will set position absolute to parent badge-container*/
  border: 1px solid black;
  height: 1rem;
  width: 1rem;
  display: flex;
  justify-content: center;
  top: 0.1rem;
  left: 2.8rem;
}

.cart,
.heart {
  width: 4rem;
  border: none;
  position: relative;
}
.border-div {
  border: 1px solid white;
  height: 20rem;
  width: 17rem;
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  display: grid;
  vertical-align: middle;
  align-items: center;
  margin: auto;
  position: relative;
}
.icon-button {
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
  position: absolute;
  top: 4rem;
  right: 2rem;
}

.flexing {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  overflow: hidden;
  align-items: center;
}

.remove_button {
  position: absolute;
  top: 0px;
  right: 0px;
}

.badge-color {
  background-color: grey;
}

.badge-color:hover {
  background-color: white;
}

.Card {
  border: 1px solid gray;
  padding: 2.5rem 2rem;
  border-radius: 0.4rem;
  margin: 3rem;
  position: relative;
}
.heading {
  position: absolute;
  top: 0px;
}

.mid-width-card {
  height: 15rem;
  width: 20rem;
}

.shadow {
  box-shadow: 10px 10px 15px;
}

.fieldset {
  display: flex;
  justify-content: center;
  border: none;
}
